.page-header {

}

.page-header__container {
  margin-top: 72px;
  display: flex;
  align-items: flex-start;
  margin-bottom: 103px;
  position: relative;

  &::before {
    content: "";
    width: 593px;
    height: 593px;
    background: rgba(255, 60, 29, 0.75);
    filter: blur(250px);
    position: absolute;
    top: -567px;
    left: 309px;
    border-radius: 50%;
  }
}

.page-header__logo {
  margin-right: 20px;
}

.page-header__description {
  padding: 25px 17px 22px 18px;
  background: rgba(196, 196, 196, 0.01);
  backdrop-filter: blur(45px);
  border-radius: 10px;
}

.page-header__description p {
  font-weight: 300;
  font-size: 18px;
  line-height: 103%;
}

.page-header__days {
  margin: 0;
}

.page-header__first {
  position: relative;
  margin: 0 0 0 6px;

  &::before {
    content: "";
    background: url("../img/circle.svg") no-repeat;
    position: absolute;
    left: -10px;
    top: 4px;
    width: 11px;
    height: 11px;
  }
}
