html {
  height: 100%;
}

.page__body {
  // opacity: 0;
  // transition: 0.9s opacity ease-in-out;
  margin: 0;
  font-family: "Neue Machina", sans-serif;
  font-size: 23px;
  line-height: 103%;
  font-weight: normal;
  color: $white;
  background: #070707;
  position: relative;
  overflow-x: hidden;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr;
  align-content: flex-start;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}

img {
  max-width: 100%;
}

.element-animation {
  opacity: 0;
  transform: translateY(20px);
}

.element-animation.element-show {
  opacity: 1;
  transition: all 1.5s;
  transform: translateY(0%);
}

.color {
  color: $red;
}
