.promo {
  overflow: hidden;
  padding-bottom: 266px;

  &::after {
    content: "";
    background: url("../img/background.jpg") no-repeat;
    width: 1920px;
    height: 1079px;
    position: absolute;
    top: 0;
    left: -10px;
    z-index: -1;
  }
}

.promo__heading {
  z-index: 2;
  margin: 0;
  font-weight: 900;
  font-size: 60px;
  line-height: 103%;
  width: 81%;
  text-transform: uppercase;
  margin-bottom: 3px;
  position: relative;

  &::after {
    content: "";
    z-index: -1;
    width: 590px;
    height: 60px;
    background: $red;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(60px);
  }
}

.promo__description {
  margin: 0;
  font-size: 30px;
  line-height: 103%;
  width: 77%;
  z-index: 2;
}

.promo__offer {
  padding: 87px 200px 80px 82px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(53.9274px);
  border-radius: 41px;
  margin-bottom: 25px;
}

.promo__wrapper {
  width: 1046px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.promo__button {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  text-decoration: none;
  background: url("../img/button.svg") no-repeat;
  width: 465.84px;
  height: 133.5px;
  font-size: 28px;
  line-height: 160%;
  font-weight: bold;
  color: #7c1100;

  &::before {
    content: "";
    z-index: -1;
    position: absolute;
    width: 308px;
    height: 124px;
    left: 50%;
    bottom: 19%;
    transform: translateX(-50%);
    background: #d8a73d;
    box-shadow: 0 26px 104px #ff3b1c;
    border-radius: 47px;
  }
}

.promo__small {
  display: inline-block;
  font-size: 20px;
  line-height: 160%;
  margin-top: -5px;
}

.promo__paragraph {
  margin: 0;
  font-size: 23px;
  line-height: 103%;
  width: 462px;
  padding: 28px 48px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(45px);
  border-radius: 32px;

  &::after {
    content: "";
    width: 60px;
    height: 60px;
    position: absolute;
    border-radius: 50%;
    top: 29px;
    right: -32px;
    background-image: url("../img/icon-fire.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.promo__image {
  position: absolute;
  top: 71px;
  right: -172px;
}
