@font-face {
  font-family: "Neue Machina";
  src: local("NeueMachina Regular"), local("NeueMachina-Regular"), url("../fonts/NeueMachina-Regular.woff") format("woff"), url("../fonts/NeueMachina-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Neue Machina";
  src: local("NeueMachina Black"), local("NeueMachina-Black"), url("../fonts/NeueMachina-Black.woff") format("woff"), url("../fonts/NeueMachina-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: "Neue Machina";
  src: local("NeueMachina Light"), local("NeueMachina-Light"), url("../fonts/NeueMachina-Light.woff") format("woff"), url("../fonts/NeueMachina-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Neue Machina";
  src: local("NeueMachina Bold"), local("NeueMachina-Bold"), url("../fonts/NeueMachina-Bold.woff") format("woff"), url("../fonts/NeueMachina-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Neue Machina";
  src: local("NeueMachina Medium"), local("NeueMachina-Medium"), url("../fonts/NeueMachina-Medium.woff") format("woff"), url("../fonts/NeueMachina-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Neue Machina";
  src: local("NeueMachina Ultrabold"), local("NeueMachina-Ultrabold"), url("../fonts/NeueMachina-Ultrabold.woff") format("woff"), url("../fonts/NeueMachina-Ultrabold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
}
html {
  height: 100%;
}

.page__body {
  margin: 0;
  font-family: "Neue Machina", sans-serif;
  font-size: 23px;
  line-height: 103%;
  font-weight: normal;
  color: #ffffff;
  background: #070707;
  position: relative;
  overflow-x: hidden;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr;
  align-content: flex-start;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}

img {
  max-width: 100%;
}

.element-animation {
  opacity: 0;
  transform: translateY(20px);
}

.element-animation.element-show {
  opacity: 1;
  transition: all 1.5s;
  transform: translateY(0%);
}

.color {
  color: #ff3b1c;
}

.page-header__container {
  margin-top: 72px;
  display: flex;
  align-items: flex-start;
  margin-bottom: 103px;
  position: relative;
}
.page-header__container::before {
  content: "";
  width: 593px;
  height: 593px;
  background: rgba(255, 60, 29, 0.75);
  filter: blur(250px);
  position: absolute;
  top: -567px;
  left: 309px;
  border-radius: 50%;
}

.page-header__logo {
  margin-right: 20px;
}

.page-header__description {
  padding: 25px 17px 22px 18px;
  background: rgba(196, 196, 196, 0.01);
  backdrop-filter: blur(45px);
  border-radius: 10px;
}

.page-header__description p {
  font-weight: 300;
  font-size: 18px;
  line-height: 103%;
}

.page-header__days {
  margin: 0;
}

.page-header__first {
  position: relative;
  margin: 0 0 0 6px;
}
.page-header__first::before {
  content: "";
  background: url("../img/circle.svg") no-repeat;
  position: absolute;
  left: -10px;
  top: 4px;
  width: 11px;
  height: 11px;
}

.promo {
  overflow: hidden;
  padding-bottom: 266px;
}
.promo::after {
  content: "";
  background: url("../img/background.jpg") no-repeat;
  width: 1920px;
  height: 1079px;
  position: absolute;
  top: 0;
  left: -10px;
  z-index: -1;
}

.promo__heading {
  z-index: 2;
  margin: 0;
  font-weight: 900;
  font-size: 60px;
  line-height: 103%;
  width: 81%;
  text-transform: uppercase;
  margin-bottom: 3px;
  position: relative;
}
.promo__heading::after {
  content: "";
  z-index: -1;
  width: 590px;
  height: 60px;
  background: #ff3b1c;
  position: absolute;
  top: 0;
  left: 0;
  filter: blur(60px);
}

.promo__description {
  margin: 0;
  font-size: 30px;
  line-height: 103%;
  width: 77%;
  z-index: 2;
}

.promo__offer {
  padding: 87px 200px 80px 82px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(53.9274px);
  border-radius: 41px;
  margin-bottom: 25px;
}

.promo__wrapper {
  width: 1046px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.promo__button {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  text-decoration: none;
  background: url("../img/button.svg") no-repeat;
  width: 465.84px;
  height: 133.5px;
  font-size: 28px;
  line-height: 160%;
  font-weight: bold;
  color: #7c1100;
}
.promo__button::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 308px;
  height: 124px;
  left: 50%;
  bottom: 19%;
  transform: translateX(-50%);
  background: #d8a73d;
  box-shadow: 0 26px 104px #ff3b1c;
  border-radius: 47px;
}

.promo__small {
  display: inline-block;
  font-size: 20px;
  line-height: 160%;
  margin-top: -5px;
}

.promo__paragraph {
  margin: 0;
  font-size: 23px;
  line-height: 103%;
  width: 462px;
  padding: 28px 48px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(45px);
  border-radius: 32px;
}
.promo__paragraph::after {
  content: "";
  width: 60px;
  height: 60px;
  position: absolute;
  border-radius: 50%;
  top: 29px;
  right: -32px;
  background-image: url("../img/icon-fire.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.promo__image {
  position: absolute;
  top: 71px;
  right: -172px;
}

.price {
  padding-bottom: 117px;
  position: relative;
}
.price::before {
  content: "";
  position: absolute;
  background: url("../img/tickets-left.png") no-repeat;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.price::after {
  content: "";
  position: absolute;
  background: url("../img/tickets-right.png") no-repeat;
  right: -1234px;
  top: -420px;
  width: 100%;
  height: 100%;
}

.price__heading {
  margin: 0;
  width: 71%;
  position: relative;
  font-weight: 900;
  font-size: 60px;
  line-height: 103%;
  text-transform: uppercase;
  margin-top: 70px;
  margin-bottom: 37px;
}
.price__heading::after {
  content: "";
  position: absolute;
  width: 422px;
  height: 49px;
  background: #ff3b1c;
  border-radius: 50%;
  top: 72px;
  left: 438px;
  filter: blur(50px);
  z-index: -1;
}
.price__heading::before {
  content: "";
  width: 313px;
  height: 49px;
  position: absolute;
  background: #ff3b1c;
  border-radius: 50%;
  left: -31px;
  top: 136px;
  filter: blur(50px);
  z-index: -1;
}

.price__paragraph {
  width: 32%;
  margin: 0 0 0 78px;
  position: relative;
  font-size: 30px;
  line-height: 103%;
  margin-right: 13px;
}
.price__paragraph::before {
  content: "";
  width: 65px;
  height: 65px;
  position: absolute;
  left: -77px;
  top: -3px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(38px);
  border-radius: 50%;
  background-image: url("../img/icon-lightning.png");
  background-repeat: no-repeat;
  background-position: center;
}

.price__wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 29px;
}

.price__feedback {
  margin: 0;
  padding: 25px 34px;
  width: 48%;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(30px);
  border-radius: 24px;
  position: relative;
}
.price__feedback::after {
  content: "";
  width: 51px;
  height: 51px;
  position: absolute;
  top: -12px;
  right: 44px;
  background: url("../img/icon-feedback.png") no-repeat;
}

.price__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  grid-template-rows: min-content;
}

.price__item {
  align-self: flex-start;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(54px);
  border-radius: 41px;
}

.price__item:nth-child(1) {
  position: relative;
}
.price__item:nth-child(1)::after {
  content: "";
  position: absolute;
  top: 60px;
  left: -50px;
  width: 186px;
  height: 186px;
  background: #ef0200;
  border-radius: 50%;
  filter: blur(100px);
  z-index: -1;
}

.price__item:nth-child(2) {
  position: relative;
}
.price__item:nth-child(2)::after {
  content: "";
  position: absolute;
  top: 130px;
  left: -50px;
  width: 186px;
  height: 186px;
  background: #ffa800;
  border-radius: 50%;
  filter: blur(100px);
  z-index: -1;
}

.price__item:nth-child(3) {
  position: relative;
}
.price__item:nth-child(3)::after {
  content: "";
  position: absolute;
  top: 100px;
  left: -50px;
  width: 186px;
  height: 186px;
  background: #ff5936;
  border-radius: 50%;
  filter: blur(100px);
  z-index: -1;
}

.price__name {
  margin: 0;
  text-transform: lowercase;
  position: relative;
  font-weight: bold;
  font-size: 40px;
  line-height: 103%;
  margin-bottom: 13px;
  margin-top: 22px;
}

.price__advantages {
  position: relative;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 16px;
  margin-bottom: 13px;
  padding-left: 30px;
  margin-left: 23px;
  padding-right: 38px;
}

.prcie__advantage {
  position: relative;
  font-size: 23px;
  line-height: 103%;
}
.prcie__advantage::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background: url("../img/li-marker.svg") no-repeat;
  background-position: left 50%;
  width: 26px;
  height: 11px;
}

.price__price {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.price__description {
  font-weight: 300;
  font-size: 11px;
  line-height: 103%;
  margin-bottom: 30px;
}

.price__item:nth-child(1) .price__name::before {
  content: "";
  background: url("../img/icon-rocket.png") no-repeat;
  width: 28px;
  height: 28px;
  position: absolute;
  left: -33px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(1) .price__name::after {
  content: "";
  background: url("../img/icon-rocket.png") no-repeat;
  width: 28px;
  height: 28px;
  position: absolute;
  right: -33px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(2) .price__name::before {
  content: "";
  background: url("../img/icon-star.png") no-repeat;
  width: 31px;
  height: 31px;
  position: absolute;
  left: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(2) .price__name::after {
  content: "";
  background: url("../img/icon-star.png") no-repeat;
  width: 31px;
  height: 31px;
  position: absolute;
  right: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(3) .price__name::before {
  content: "";
  background: url("../img/icon-fire.png") no-repeat;
  background-size: cover;
  width: 31px;
  height: 31px;
  position: absolute;
  left: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(3) .price__name::after {
  content: "";
  background: url("../img/icon-fire.png") no-repeat;
  background-size: cover;
  width: 31px;
  height: 31px;
  position: absolute;
  right: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__current-price {
  font-weight: 800;
  font-size: 40px;
  line-height: 160%;
  margin: 0;
  margin-right: 6px;
}

.price__old-price {
  font-weight: 500;
  font-size: 24px;
  line-height: 160%;
  margin: 0;
  text-decoration: line-through;
  color: #737373;
}

.price__button {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  text-decoration: none;
  background: url("../img/button-price.svg") no-repeat;
  width: 294px;
  height: 84px;
  font-size: 18px;
  line-height: 160%;
  font-weight: bold;
  color: #7c1100;
}
.price__button::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 194px;
  height: 78px;
  left: 50%;
  bottom: 25%;
  transform: translateX(-50%);
  background: #d8a73d;
  box-shadow: 0 -3px 65px #ff3b1c;
  border-radius: 47px;
}

.price__small {
  display: inline-block;
  line-height: 160%;
  margin-top: -5px;
  font-size: 12px;
}