.price {
  padding-bottom: 117px;

  position: relative;

  &::before {
    content: "";
    position: absolute;
    background: url("../img/tickets-left.png") no-repeat;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &::after {
    content: "";
    position: absolute;
    background: url("../img/tickets-right.png") no-repeat;
    right: -1234px;
    top: -420px;
    width: 100%;
    height: 100%;
  }
}

.price__heading {
  margin: 0;
  width: 71%;
  position: relative;
  font-weight: 900;
  font-size: 60px;
  line-height: 103%;
  text-transform: uppercase;
  margin-top: 70px;
  margin-bottom: 37px;

  &::after {
    content: "";
    position: absolute;
    width: 422px;
    height: 49px;
    background: $red;
    border-radius: 50%;
    top: 72px;
    left: 438px;
    filter: blur(50px);
    z-index: -1;
  }

  &::before {
    content: "";
    width: 313px;
    height: 49px;
    position: absolute;
    background: $red;
    border-radius: 50%;
    left: -31px;
    top: 136px;
    filter: blur(50px);
    z-index: -1;
  }
}

.price__paragraph {
  width: 32%;
  margin: 0 0 0 78px;
  position: relative;
  font-size: 30px;
  line-height: 103%;
  margin-right: 13px;

  &::before {
    content: "";
    width: 65px;
    height: 65px;
    position: absolute;
    left: -77px;
    top: -3px;
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(38px);
    border-radius: 50%;
    background-image: url("../img/icon-lightning.png");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.price__wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 29px;
}

.price__feedback {
  margin: 0;
  padding: 25px 34px;
  width: 48%;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(30px);
  border-radius: 24px;
  position: relative;

  &::after {
    content: "";
    width: 51px;
    height: 51px;
    position: absolute;
    top: -12px;
    right: 44px;
    background: url("../img/icon-feedback.png") no-repeat;
  }
}

.price__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  grid-template-rows: min-content;
}

.price__item {
  align-self: flex-start;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(54px);
  border-radius: 41px;
}

.price__item:nth-child(1) {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 60px;
    left: -50px;
    width: 186px;
    height: 186px;
    background: #ef0200;
    border-radius: 50%;
    filter: blur(100px);
    z-index: -1;
  }
}

.price__item:nth-child(2) {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 130px;
    left: -50px;
    width: 186px;
    height: 186px;
    background: #ffa800;
    border-radius: 50%;
    filter: blur(100px);
    z-index: -1;
  }
}

.price__item:nth-child(3) {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 100px;
    left: -50px;
    width: 186px;
    height: 186px;
    background: #ff5936;
    border-radius: 50%;
    filter: blur(100px);
    z-index: -1;
  }
}

.price__name {
  margin: 0;
  text-transform: lowercase;
  position: relative;
  font-weight: bold;
  font-size: 40px;
  line-height: 103%;
  margin-bottom: 13px;
  margin-top: 22px;
}

.price__advantages {
  position: relative;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 16px;
  margin-bottom: 13px;
  padding-left: 30px;
  margin-left: 23px;
  padding-right: 38px;
}

.prcie__advantage {
  position: relative;
  font-size: 23px;
  line-height: 103%;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    background: url("../img/li-marker.svg") no-repeat;
    background-position: left 50%;
    width: 26px;
    height: 11px;
  }
}

.price__price {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.price__description {
  font-weight: 300;
  font-size: 11px;
  line-height: 103%;
  margin-bottom: 30px;
}

.price__item:nth-child(1) .price__name::before {
  content: "";
  background: url("../img/icon-rocket.png") no-repeat;
  width: 28px;
  height: 28px;
  position: absolute;
  left: -33px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(1) .price__name::after {
  content: "";
  background: url("../img/icon-rocket.png") no-repeat;
  width: 28px;
  height: 28px;
  position: absolute;
  right: -33px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(2) .price__name::before {
  content: "";
  background: url("../img/icon-star.png") no-repeat;
  width: 31px;
  height: 31px;
  position: absolute;
  left: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(2) .price__name::after {
  content: "";
  background: url("../img/icon-star.png") no-repeat;
  width: 31px;
  height: 31px;
  position: absolute;
  right: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(3) .price__name::before {
  content: "";
  background: url("../img/icon-fire.png") no-repeat;
  background-size: cover;
  width: 31px;
  height: 31px;
  position: absolute;
  left: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__item:nth-child(3) .price__name::after {
  content: "";
  background: url("../img/icon-fire.png") no-repeat;
  background-size: cover;
  width: 31px;
  height: 31px;
  position: absolute;
  right: -34px;
  top: 50%;
  transform: translateY(-50%);
}

.price__current-price {
  font-weight: 800;
  font-size: 40px;
  line-height: 160%;
  margin: 0;
  margin-right: 6px;
}

.price__old-price {
  font-weight: 500;
  font-size: 24px;
  line-height: 160%;
  margin: 0;
  text-decoration: line-through;
  color: $gray;
}

.price__button {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  text-decoration: none;
  background: url("../img/button-price.svg") no-repeat;
  width: 294px;
  height: 84px;
  font-size: 18px;
  line-height: 160%;
  font-weight: bold;
  color: #7c1100;

  &::before {
    content: "";
    z-index: -1;
    position: absolute;
    width: 194px;
    height: 78px;
    left: 50%;
    bottom: 25%;
    transform: translateX(-50%);
    background: #d8a73d;
    box-shadow: 0 -3px 65px #ff3b1c;
    border-radius: 47px;
  }
}

.price__small {
  display: inline-block;
  line-height: 160%;
  margin-top: -5px;
  font-size: 12px;
}
